*, ::before, ::after {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
  padding: 0;
}

button:hover,
a:hover,
button:focus,
a:focus {
  cursor: pointer;
}

h1 {
  font-size: 5rem;
  font-weight: 700;
  line-height: 1.5;
  color: #388AF3;
}
@media screen and (max-width: 1299px) {
  h1 {
    font-size: 3.75rem;
    line-height: 2;
  }
}
@media screen and (max-width: 767px) {
  h1 {
    font-size: 2.5rem;
    line-height: 1;
  }
}

.page {
  max-width: 100vw;
  overflow-x: hidden;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
@media screen and (max-width: 1299px) {
  .container {
    max-width: 100%;
    padding: 0 2.5rem;
  }
}

.col {
  flex: 1;
}

.logotype > a {
  color: #303030;
  font-size: 2.5rem;
  font-weight: 800;
  line-height: 1.25;
  text-decoration: none;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
