    font-size: 1.875rem;
    line-height: 1.26;
  }
}
.logotype > a:hover {
  opacity: 0.75;
}
.logotype--white > a {
  color: #FFFFFF;
}

@keyframes submitedMessage {
  from {
    width: 6rem;
  }
  to {
    width: 1.75rem;
  }
}
@keyframes submitedSpan {
  0% {
    opacity: 1;
  }
  33% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 1149px) {
  .form {
    flex-direction: column;
  }
}
.form__privacy-policy {
  margin-top: -2rem;
  font-size: 1.125rem;
  line-height: 1.5;
  color: #6D6D6D;
  font-family: "Circe", sans-serif;
  padding: 0 0 0 3.125rem;
  position: relative;
}
@media screen and (max-width: 1149px) {
  .form__privacy-policy {
    margin-top: -1rem;
  }
}
.form__privacy-policy a {
  color: #388AF3;
  text-decoration: none;
  position: relative;
}
.form__privacy-policy a::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0.125rem;
  border-bottom: 1px solid transparent;
  transition: border-bottom-color 0.3s ease;
}
.form__privacy-policy a:hover::after {
  border-bottom-color: #388AF3;
}
.form__privacy-policy > span {
  width: 1.875rem;
  height: 1.875rem;
  border-radius: 50%;
  border: 1px solid #D3D3D3;
  position: absolute;
  left: 0;
  top: 45%;
  transform: translateY(-55%);
  transition: border-color 0.3s ease;
  overflow: hidden;
}
.form__privacy-policy > span::before {
  content: "";
  position: absolute;
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 50%;
  background-color: #fff;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  transition: transform 0.3s ease, background-color 0.3s ease;
}
.form__privacy-policy > span:hover {
  border-color: #388AF3;
  cursor: pointer;
}
.form__privacy-policy > input {
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
  opacity: 0;
  pointer-events: none;
}
.form__privacy-policy > input:checked + span::before {
  background-color: #388AF3;
  transform: translate(-50%, -50%) scale(1);
}
.form__button-submit {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding: 0 3.125rem 1.4rem;
}
@media screen and (max-width: 1149px) {
  .form__button-submit {
    justify-content: flex-start;
    align-items: flex-start;
    padding: 3.875rem 0 0;
  }
}
@media screen and (max-width: 767px) {
  .form__button-submit {
    padding: 2.1875rem 0 0;
  }
}
.form__button-submit button {
  background: none;
  outline: none;
  border: 0;
  width: 14.1875rem;
  height: 14.1875rem;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  font-size: 1.25rem;
  line-height: 1;
}
.form__button-submit button > span {
  position: relative;
  z-index: 3;
  color: #388AF3;
  transform: translateY(1.875rem);
  transition: all 0.3s ease;
}
.form__button-submit button > img {
  position: absolute;
  z-index: 3;
  top: 4rem;
  right: 5rem;
  width: 3.5625rem;
  transition: all 0.3s ease;
}
.form__button-submit button::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 50%;
  background: linear-gradient(93.29deg, #388AF3 21.04%, #A483E2 88.77%);
  z-index: 1;
}
.form__button-submit button::after {
  content: "";
  position: absolute;
  left: 0.325rem;
  right: 0.325rem;
  top: 0.325rem;
  bottom: 0.325rem;
  background-color: #fff;
  border-radius: 50%;
  z-index: 2;
  transition: all 0.3s ease;
}
.form__button-submit button:hover > span {
  color: #fff;
  transform: translateY(0);
}
.form__button-submit button:hover > img {
  right: 0;
  top: 0;
  width: 6rem;
}
.form__button-submit button:hover::after {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #388AF3;
}
.form__button-submit button.animated > span {
  color: #fff;
  transform: translateY(0);
  animation: submitedSpan 0.5s forwards linear;
}
.form__button-submit button.animated > img {
  right: 0;
  top: 0;
  width: 6rem;
  animation: submitedMessage 0.5s forwards ease;
}
.form__button-submit button.animated::after {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #388AF3;
}

.field {
  position: relative;
}
.field__placeholder {
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(0.5rem);
  font-size: 1.375rem;
  line-height: 1.19;
  pointer-events: none;
  color: #CCC;
  font-weight: 300;
  transition: font-size 0.2s ease, transform 0.2s ease;
}
@media screen and (max-width: 767px) {
  .field__placeholder {
    font-size: 1.375rem;
    transform: translateY(0.25rem);
  }
}
.field__placeholder--required::after {
  content: "*";
  margin-left: 0.3rem;
  color: red;
}
.field__placeholder--active {
  transform: translateY(-1.25rem);
  font-size: 1rem;
}
.field input, .field textarea {
  background: none;
  outline: none;
  border: 0;
  border-bottom: 1px solid #E2E2E2;
  font-size: 1.625rem;
  line-height: 1.19;
  padding: 0.25rem 0;
  width: 100%;
  resize: none;
}
