.navigation {
  list-style-type: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}
.navigation__item {
  font-weight: 700;
  margin-right: 1.125rem;
  position: relative;
}
.navigation__item:first-child > a {
  color: #343434;
}
.navigation__item::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: -0.05rem;
  background-color: #388AF3;
  border-radius: 50%;
  width: 0.4375rem;
  height: 0.4375rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.navigation__item:last-child {
  margin-right: 0;
}
.navigation__item > a {
  display: block;
  text-decoration: none;
  color: #282828;
  padding: 0.25rem 1rem 0.65rem;
  transition: color 0.3s ease;
  font-weight: 600;
  font-size: 1.125rem;
  line-height: 1.16;
}
.navigation__item:hover::after, .navigation__item:focus::after {
  opacity: 1;
  transform: scale(1);
}
.navigation__item:hover > a, .navigation__item:focus > a {
  color: #388AF3;
}
.navigation__item--active::after {
  opacity: 1;
  transform: scale(1);
}
.navigation__item--active > a {
  color: #388AF3;
  font-weight: 700;
  line-height: 1.22;
}

.language {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
}
.language__item {
  position: relative;
  margin-right: 0.9375rem;
}
.language__item:last-child {
  margin-right: 0;
}
.language__item::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: -0.425rem;
  background-color: #388AF3;
  border-radius: 50%;
  width: 0.25rem;
  height: 0.25rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
}
.language__item > a {
  color: #979BAD;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.3s ease;
  font-weight: 600;
  line-height: 1.1875;
  letter-spacing: 0.02em;
}
.language__item--active::after, .language__item:hover::after, .language__item:focus::after {
  opacity: 1;
  transform: scale(1);
}
.language__item--active > a, .language__item:hover > a, .language__item:focus > a {
  color: #388AF3;
}

.breadcrumb {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 5rem 0 2.5rem;
}
.page__contacts .breadcrumb {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
@media screen and (max-width: 1299px) {
  .page__contacts .breadcrumb {
    max-width: 100%;
    padding: 4rem 2.625rem 2.5rem;
  }
}
@media screen and (max-width: 767px) {
  .page__contacts .breadcrumb {
    padding: 5rem 2.625rem 3.875rem;
  }
}
.breadcrumb__item {
  position: relative;
  margin-right: 1.125rem;
}
.breadcrumb__item > a {
  text-decoration: none;
  transition: color 0.3s ease;
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 1.16;
  color: #303030;
}
@media screen and (max-width: 1299px) {
  .breadcrumb__item > a {
    font-size: 1rem;
    line-height: 1.125;
  }
}
.breadcrumb__item::after {
  content: "-";
  position: absolute;
  right: -0.75rem;
  top: 50%;
  transform: translateY(-50%);
  font-weight: 400;
}
.breadcrumb__item--active > a {
  cursor: default;
  pointer-events: none;
  color: #303030;
  font-weight: 600;
}
.breadcrumb__item--active::after {
  display: none;
}
.breadcrumb__item:not(.breadcrumb__item--active):hover > a, .breadcrumb__item:not(.breadcrumb__item--active):focus > a {
  color: #388AF3;
}

.header {
