  background: none;
  border: 0;
  outline: none;
  position: relative;
  padding: 1.25rem 2.375rem 1.25rem 1.5rem;
}
.header__start-project::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 3.8125rem;
  width: 3.8125rem;
  border: 1px solid #CCC;
  border-radius: 2rem;
  transition: width 0.5s ease, color 0.25s linear 0.25s;
  pointer-events: none;
  z-index: 1;
