.header__start-project::after {
  content: "";
  position: absolute;
  right: 0.9375rem;
  top: 50%;
  width: 0.8125rem;
  height: 0.75rem;
  transform: translateY(-50%);
  background-image: url("../img/arrow.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  pointer-events: none;
  z-index: 2;
}
.header__start-project > span {
  text-transform: uppercase;
  color: #388AF3;
  position: relative;
  z-index: 3;
  font-weight: 800;
  font-size: 1rem;
  line-height: 1.25;
  letter-spacing: 0.02em;
}
